.bg {
  background-color: lightpink;
}

.cover {
  background-image: linear-gradient(185deg, rgba(0, 0, 0, 0.69) 9%, rgba(0, 0, 0, 0) 100%), url('../assets/pic/RS_7.jpg');
  position: fixed;
  z-index: 11;
  height: 100vh;
  width: 100%;
  top: 0px;
  display: flex;
  flex-direction: column;
  background-position: 43% 30%;
  align-items: center;
}

.move {
  top: -100vh;
  transition: all 1s;
}

.buttonPlayPause {
  font-size: 30px;
  position: fixed;
  bottom: 10px;
  right: 10px;
  color: white;
}

.button {
  margin-top: 30px;
  width: 160px;
  height: 35px;
  border-radius: 20px;
  border: none;
  font-size: 16px;
  cursor: pointer;
}

.button::after {
  font-size: 18px;
  line-height: 1em;
  content: ">";
  opacity: 0;
  margin-left: -1em;
  -webkit-transition: all .2s;
  transition: all .2s;
  text-transform: none;
  -webkit-font-feature-settings: "kern" off;
  font-feature-settings: "kern" off;
  font-variant: none;
  font-style: normal;
  font-weight: 400;
  text-shadow: none;
  transition: all 300ms ease 0ms;
}

.button:hover::after {
  opacity: 1;
  /* Makes the arrow visible on hover */
  width: 170px;
  margin-left: 10px;
}

.paper {
  background-color: #fffaf6;
  border-radius: 15px 15px 15px 15px;
  overflow: hidden;
  border-width: 0px;
  border-color: #fbd2da;
  box-shadow: 0px 5px 20px -10px #000000;
  padding: 90px 0px;
  margin: 50px 30px;
}

.img {
  width: 150px;
  filter: opacity(0.5);
}

.text {
  padding: 25px 45px 0px 45px;
  font-size: 15px;
  line-height: 2em;
}

.R{
  max-width: 250px;
  border-radius: 50px;
  box-shadow: 0px 5px 20px -10px #000000;
  margin-top: 80px;
}